<template>
  <div class="container">
    <div class="row">
      <div class="col-md-10">
        <h1>{{board.title}}</h1>
        <h5>{{board.id}}</h5>
      </div>
      <div class="col-md-2">
        <div><br><br></div>
        <div><button class="btn btn-warning" @click="update(board.no)">수정</button></div>
      </div>
    </div>
    <hr />
    <p class="text-left">{{board.contents}}</p>
    <button class="btn btn-success" @click="showlist">목록</button>
  </div>
</template>

<script>
import axios from "../js/http-commons";
import router from "../assets/router";

export default {
  props: ["no"],
  data() {
    return {
      board: {},
      id: "",
    };
  },
  mounted() {
    axios
      .post("/searchNo/" + this.no)
      .then(res => {
        console.log(res.data);
        this.board = res.data;
      })
      .catch(e => {
        console.log(e);
      })
      .finally(() => {
        console.log("글 찾음");
      });

      axios
        .post("/getId")
        .then(res => {
          console.log("get id", res);
          this.id = res.data.id;
        })
        .catch(e => {
            alert("로그인하세요");
            console.log(e);
            router.push({
            path: "/"
          });
          })
          .finally(() => {
            console.log("finally id");
          })
  },
  methods: {
    update(no) {
      if(this.id == this.board.id){
      router.push({
        path: "/update/" + no
      });
      }else{
        alert("남의 글은 수정할 수 없어요");
      }
    },
    showlist() {
      router
        .push({
          path: "/"
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
button {
  margin-left: 10px;
}
h1 {
  text-align: left;
}
h5 {
  text-align: left;
}
p {
  height: 300px;
}
</style>