<template>
  <div class="container">
    <input type="text" class="form-control" v-model="title" />
    <br />
    <textarea class="form-control" rows="20" v-model="contents"></textarea>
    <br />
    <button class="btn btn-warning" @click="update">수정</button>
    <button class="btn btn-danger" @click="deletepost">삭제</button>

  </div>
</template>

<script>
import axios from "../js/http-commons";
import router from "../assets/router";

export default {
  props: ["no"],
  data() {
    return {
      title: "",
      contents: "",
      board: {}
    };
  },
  mounted() {
    axios
      .post("/searchNo/" + this.no)
      .then(res => {
          this.board = res.data;
          this.title = res.data.title;
            this.contents = res.data.contents;
          console.log(this.board);
      })
      .catch(e => {
        console.log(e);
      })
      .finally(() => {
        console.log("update 글 찾아옴");
      });
  },
  methods: {
    deletepost() {
      axios
        .delete("/deleteBoard/" + this.no)
        .then(res => {
          console.log(res.data);
          router.push({
            path: "/"
          });
        })
        .catch(e => {
          console.log(e);
        })
        .finally(() => {
            alert("글 삭제 완료")
          console.log("글 삭제 완료");
        });
    },
    update() {
      this.board.title = this.title;
      this.board.contents = this.contents;
      console.log(this.board);
      axios
        .put("/updateBoard", this.board)
        .then(res => {
          console.log(res.data);
          router.push({
            path: "/board/" + this.no
          });
        })
        .catch(e => {
          console.log(e);
        })
        .finally(() => {
          console.log("글 수정 완료");
        });
    }
  }
};
</script>

<style scoped>
button {
  margin-left: 5px;
}
</style>