<template>
  <div class="container">
    <div class="container">
      <div class="row">
        <div class="col-md-3">
          <p></p>
        </div>
        <div class="col-md-5">
          <input type="text" class="form-control" v-model="findtext" />
        </div>
        <div class="col-md-2">
          <button type="submit" class="btn btn-success" @click="find">검색</button>
        </div>
      </div>
    </div>
    <br />
    <table class="table">
      <tr>
        <th>글번호</th>
        <th>제 목</th>
        <th>작성자</th>
      </tr>
      <tr v-for="(board) in boards" :key="board.no" @click="detail(board.no)">
         <td id="number">{{board.no}}</td>
         <td id="title">{{board.title}}</td> 
         <td id="id">{{board.id}}</td>
      </tr>
    </table>

    <div class="row">
      <div class="col-md-10">
        <p></p>
      </div>
      
      <div class="col-md-2">
        <button type="submit" class="btn btn-info" @click="write">글쓰기</button>
      </div>
    </div>
  </div>
</template>

<script>
import router from "../assets/router";
import axios from "../js/http-commons";

export default {
  router,
  data() {
    return {
      findtext: "",
      boards: []
    };
  },
  mounted() {
    axios
      .post("/searchAll")
      .then(res => {
        console.log(res.data);
        this.boards = res.data;
      })
      .catch(e => {
        console.log(e);
      })
      .finally(() => {
        console.log("글 전체 가져옴");
      });
  },
  methods: {
    detail(no) {
      router.push({
        path: "/board/" + no
      });
    },
    find() {
      console.log("검색어: "+this.findtext);
      if(this.findtext == undefined){
        this.findtext = "";
      }
      axios
        .post("/searchContent/"+this.findtext)
        .then(res => {
            console.log(res.data)
          this.boards = res.data;
        })
        .catch(e => {
          console.log(e.response);
        })
        .finally(() => {
          console.log(this.findtext, "검색 완료");
        });
    },
    write() {
      router.push({
        path: "/write"
      });
    }
  }
};
</script>

<style scoped>
#number{
    width: 10%
}
#title{
    width: 80%;
    text-align: left;
}
#id{
    width:10%;
}
</style>