<template>
  <div class="container">
      <div class="row">
      <div class="col-md-9">
      <input type="text" class="form-control" v-model="title" placeholder="제목" />
      </div>
      <div class="col-md-3">
      <input type="text" class="form-control" v-model="boardid" placeholder="작성자"/>
      </div>
      </div>
    <br />
    <textarea class="form-control" rows="20" v-model="contents" placeholder="내용"></textarea>
    <br />
    <button class="btn btn-info" @click="write">글쓰기</button>
  </div>
</template>

<script>
import axios from "../js/http-commons";
import router from "../assets/router";

export default {
  data() {
    return {
        no: 9999,
        boardid: "",
      title: "",
      contents: "",
      board: {},
    };
  },
  mounted() {
    axios
          .post("/getId")
          .then(res => {
            console.log("get id", res);
            this.boardid = res.data.id;
          })
          .catch(e => {
            alert("로그인 안함");
            console.log(e);
            router.push({
            path: "/"
          });
          })
          .finally(() => {
            console.log("finally id");
          })
  },

  methods: {
    write() {
      this.board.id = this.boardid;
      this.board.no = this.no;
      this.board.title = this.title;
      this.board.contents = this.contents;
      console.log(this.board);
      axios
        .post("/insertBoard", this.board)
        .then(res => {
          console.log(res.data);
          router.push({
            path: "/"
          });
        })
        .catch(e => {
            alert("뭔가 잘못됨 아마 글번호 중복");
          console.log(e);
        })
        .finally(() => {
          console.log("글 작성");
        });
        
    }
  }
};
</script>

